.header{
  background-color: #fff;
  border: 1px solid rgb(223, 219, 219);
  display: grid;
  grid-template-columns: 1fr;
  position:fixed;
  top:0;
  z-index: +2;
  align-items: center;
}
.webHeader{
  display: grid;
  grid-template-columns: 2fr 1fr;
  align-items: center;
  padding: 0px 20px;
  .actions{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  }
  .cartIconWrapper{
    img{
      width: 23%;
      height: 23%;
    }
  }
  .buttonWrapper{
    padding: 0px 20px;
  }
}
.mobileHeader{display: none;}
h2{
  img{
      text-align:center;
      max-width:25px;
      padding-right: 5px;
      align-items: center;
  }
}
.cartIconWrapper{
  display: flex;
  align-items: center;
  justify-content: center;
  p{
    position: relative;
    bottom:10px;
    right: 5px;
    background-color: lime;
    padding: 3px 5px;
  }
  .cartLogo{
  margin-right: 10px;
  width: 40%;
  height: 40%;
}}

@media (max-width:600px){
.webHeader{display: none;}
.mobileHeader{
  width: 103vw;
  padding: 10px;
  display: grid;
  grid-template-columns:1fr 1fr 1fr ;
  align-items: center;
  .icon{
    padding: 0px 5px;
    img{
      width: 23%;
      height: 23%;
    }
  }
  .mobNav{
    text-align: right;
    .cartIconWrapper{
      p{
        position: relative;
        bottom:10px;
        right: 5px;
        background-color: lime;
        padding: 3px 5px;
      }
      img{
      width: 22%;
      height: 22%;
    }
  }
  }
}
}