.phoneMenu,.phoneMenuWrapper{
    position: absolute;
    display: flex;
    flex-direction: column;
    z-index: 5;
}
.phoneMenuWrapper{
    width: 100vw;
    height: 100vh;
    padding: 20px;
    background-color: rgba(0, 0, 0, 0.8);
    top: 0px;
    left: 0px;   
}
.phoneMenu{
    width: 85vw;
    height: 100vh;
    background-color: white;
    top: -1px;
    left: -1px;
    .closeMenu{
        background-color:#d51f1f;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 9px 16px;  
        color: white;
        &:hover{
            cursor: pointer;
        }
    }
    .phoneMenuAuth{
        padding: 20px 60px;
    }
    .links{
        padding: 10px 20px;
        h3
        {color:#6ce001 ;}
        .lineWrapper{
            padding:10px 0px;
        }
        .phoneMenuCategories{
            padding: 0 25px;
            a{
                text-decoration: none;
                color:black ;
                h4{
                    color: rgb(105, 104, 104);
                    padding-bottom: 8px;
                    padding-top: 8px;
                    border-bottom: 1px solid rgb(223, 222, 222);
                }
                &:hover{
                    color:#6ce001 ;
                }        
            }
        }
    }
    a{
        text-decoration: none;
        color:black ;
        &:hover{
            color:#6ce001 ;
        }        
    }
}
.socialMediaPages{ 
    padding: 20px 0px; 

}


@media (min-width: 600px) {
    .phoneMenuAuth{display: none;}
    .phoneMenu{
        width: 60vw;}

}