.categoriesHeader{
    display: flex;
    align-items: center;
    justify-content: center;
}
.benefitsWrapper{
    padding: 60px;
}
.categories{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-column-gap: 20px;
    grid-row-gap: 25px;
    padding: 10px 30px;
    a{
        background-color: lime;;
        text-decoration: none;
        h4{
            padding: .6em;
            text-align: center;
            color: white;
        }
        h4:hover{
                color: blue;
        }
    }
}
.promotions{
    background-color: rgba(243, 239, 239, 0.658);
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
}
.categorySliderWrapper{
    padding: 10px 40px;
    .advertBetween{
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        img{
            width: 100%;
            height: 100%;
        }
    }
    
}
.hspacer{
    height:25px;
    background-color: white;
}

.showMobile{display: none;}
@media (max-width:600px){
    .showWeb{display: none;}
    .showMobile{display:inline;}
    .categorySliderWrapper{
        padding: 10px;
    }
    .categories{
        display: flex;
        flex-direction: column;
        a{
            margin:5px;
        }
    }
}